  opacity: 0.8;
  top: 2px;
  transition: 400ms;
  margin-right: 30px;
}
.nav__logo > img {
  width: 36px;
  height: 44px;
  transition: 400ms;
}
.nav__links {
  width: 100%;
}
.nav__links > ul {
  display: -webkit-flex;
  display: flex;
}
.nav__links > ul > li {
  list-style: none;
  padding: 2px 40px 0px 17.3px;
  opacity: 1;
  transition: 400ms;
}
