.nav__container {
  width: 100%;
  height: auto;
  background-color: #333;
  z-index: 10;
  position: relative;
}

.nav__container-child {
  width: 980px;
  height: 100%;
  margin: auto;
  display: block;
  display: flex;
}
.nav__logo {
  width: auto;
  position: relative;
  opacity: 0.8;
  top: 2px;
  transition: 400ms;
  margin-right: 30px;
}
.nav__logo > img {
  width: 36px;
  height: 44px;
  transition: 400ms;
}
.nav__links {
  width: 100%;
}
.nav__links > ul {
  display: flex;
}
.nav__links > ul > li {
  list-style: none;
  padding: 2px 40px 0px 17.3px;
  opacity: 1;
  transition: 400ms;
}
/* .nav__links > ul > li:nth-child(8) {
  list-style: none;
  padding: 0px 20px 0px 15px;
} */
.nav__links > ul > li > a:hover {
  color: #fff;
}

.nav__links > ul > li > a {
  text-decoration: none;
  font-family: "sf pro text", "Myriad Set Pro", "SF Pro Icons", "Helvetica Neue",
    "Helvetica", "Arial", sans-serif;
  color: #d6d6d6;
  font-size: 13.5px;
  line-height: 3.14286;
  font-weight: 400;
  letter-spacing: -0.01em;
  position: relative;
  display: inline-block;
  padding: 0 10px;
  height: 41px;
  background: no-repeat;
  text-decoration: none;
  white-space: nowrap;
  -webkit-tap-highlight-color: rgb(214 214 214);
  outline-offset: -7px;
  transition: 400ms;
}
.nav__links > ul > li > a > img {
  width: 38px;
  height: 44px;
}
.nav__links > ul > li:nth-child(8) {
  padding: 7px 45px 0px 0px;
}
.nav__links > ul > li:nth-child(9) {
  padding: 7px 0px 0px 0px;
}
.nav__mobile {
  display: none;
  padding: 19px 0px 0px 0px;
}
.nav__mobile:nth-child(1) {
  margin-left: 17px;
}
.nav__mobile > svg {
  position: relative;
  right: -13px;
}
.nav__mobile_btn {
  position: absolute;
  width: 17px;
  height: 17px;
  z-index: 1;
  cursor: pointer;
}
.nav__mobile__bar__1 {
  margin-top: 5px;
  width: 19px;
  height: 1px;
  background-color: #d6d6d6;
  transition: 300ms;
  position: absolute;
  margin-top: 2px;
}
.nav__mobile__bar__2 {
  margin-top: 5px;
  width: 19px;
  height: 1px;
  background-color: #d6d6d6;
  transition: 300ms;
  position: absolute;
  margin-top: 10px;
}
.nav__mobile > svg {
  color: #d6d6d6;
  margin-right: 30px;
  margin-top: -5px;
}
.nav__search__btn__l {
  cursor: pointer;
}
/* search */
.nav__container__overlay {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #000;
  transition: 300ms;
  opacity: 0;
  pointer-events: none;
}
.nav__search__container {
  position: absolute;
  top: 0;
  left: 24.56667%;
  width: 45%;
  height: auto;
  z-index: 999;
  transition: 400ms;
  visibility: hidden;
  opacity: 0;
}
.nav__search__results {
  transition: 100ms;
  opacity: 0;
  visibility: hidden;
}
.nav__search__input__contain {
  position: relative;
  width: 100%;
  height: auto;
}
.nav__search__input__icon {
  left: 40px;
  position: relative;
  z-index: 1;
  transition: 600ms;
  opacity: 0;
}
.nav__search__input__field {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: 17px;
  line-height: 1.29412;
  letter-spacing: -0.021em;
  font-family: "sf pro text", "Myriad Set Pro", "SF Pro Icons", "Helvetica Neue",
    "Helvetica", "Arial", sans-serif;
  color: #fff;
  outline: none;
  width: 88%;
  height: 47px;
  position: absolute;
  padding: 1px 38px 0 10px;
  border: 0px;
  background-color: #1b1b1b;
  left: 70px;
  transition: 600ms;
  opacity: 0;
}
.nav__search__input__field::placeholder {
  font-family: "sf pro text", "Myriad Set Pro", "SF Pro Icons", "Helvetica Neue",
    "Helvetica", "Arial", sans-serif;
}
.nav__search__input__container > svg {
  color: #717175;
  padding: 15px 0px 9px 10px;
}
.nav__search__close {
  width: 30px;
  height: 30px;
  position: absolute;
  right: -60px;
  top: 25px;
}
.nav__search__close__btn {
  width: 30px;
  height: 30px;
  position: absolute;
  right: 5px;
  top: -15px;
  cursor: pointer;
  z-index: 10;
}
.nav__search__close__line__1 {
  width: 19px;
  height: 1px;
  background-color: #86868b;
  transition: 300ms;
  transform: rotate(45deg);
}
.nav__search__close__line__2 {
  width: 19px;
  height: 1px;
  background-color: #86868b;
  transition: 300ms;
  transform: rotate(-45deg);
  position: relative;
  top: -1px;
}
.nav__search__results {
  width: 100%;
  height: auto;
  background-color: #fff;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  padding: 20px 30px 30px 30px;
}
.nav__search__result__quick_title {
  font-size: 12px;
  line-height: 1.33337;
  letter-spacing: -0.01em;
  font-family: "sf pro text", "Myriad Set Pro", "SF Pro Icons", "Helvetica Neue",
    "Helvetica", "Arial", sans-serif;
  text-transform: uppercase;
  color: #6e6e73;
  padding: 0 !important;
  margin-bottom: 10px;
}
.nav__search__result__quick_links {
  margin-block-start: 0em;
  margin-block-end: 0em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  padding-inline-start: 0px !important;
}
.nav__search__result__quick_links__li {
  list-style: none;
  transition: 600ms;
  position: relative;
  left: 140px;
  opacity: 0;
}
.nav__search__result__quick_links > li > a {
  overflow: hidden;
  color: #1d1d1f;
  display: block;
  text-decoration: none;
  font-family: "sf pro text", "Myriad Set Pro", "SF Pro Icons", "Helvetica Neue";
  font-size: 14px;
  font-weight: inherit;
  line-height: inherit;
  text-align: inherit;
  padding: 7px 0px 7px 20px;
}
.nav__search__result__quick_links > li:hover {
  background-color: #e8e8e8;
  border-radius: 5px;
}
.nav__search__result__quick_links > li > a:hover {
  color: #0071e3;
}
.nav__mobile__v::-webkit-scrollbar {
  display: none;
}
.nav__mobile__v {
  width: 100%;
  height: 0px;
  transition: 550ms;
  background-color: #000000;
  position: relative;
  overflow-y: auto;
  overflow-x: hidden;
}
.nav__mobile__v__d {
  width: 100%;
  height: 40px;
}
.nav__mobile__v__input {
  width: 100%;
  height: inherit;
  display: flex;
  /* padding: 2px 0px 0px 15px; */
  overflow: hidden;
  transition: 1s ease;
}
.nav__mobile__v__search__input__field {
  width: 100%;
  font-size: 17px;
  line-height: 1.23536;
  font-weight: 400;
  letter-spacing: -0.022em;
  font-family: "sf pro text", "Myriad Set Pro", "SF Pro Icons", "Helvetica Neue",
    "Helvetica", "Arial", sans-serif;
  padding: 8px 15px 8px 10px;
  position: relative;
  background-color: #1d1d1f;
  border: 0;
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
  outline: none;
  cursor: text;
  color: #fff;
  transition: 430ms;
  z-index: 10;
  box-sizing: border-box;
}
.nav__mobile__v__search__input__field::placeholder {
  font-size: 17px;
  position: relative;
}
.nav__mobile__v__input__icon {
  background-color: #1d1d1f;
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;
  padding: 0px 0px 0px 8px;
  color: #6e6e73;
  height: inherit;
}
.nav__mobile__v__search__input__field__cancel {
  border: 0;
  outline: none;
  font-size: 17px;
  line-height: 1.23536;
  font-weight: 400;
  letter-spacing: -0.022em;
  font-family: "sf pro text", "Myriad Set Pro", "SF Pro Icons", "Helvetica Neue",
    "Helvetica", "Arial", sans-serif;
  color: #2997ff;
  background-color: transparent;
  margin-top: 10px;
  position: absolute;
  right: 0px;
  opacity: 0;
  transition: 800ms;
  cursor: pointer;
}
.nav__mobile__v__search__quick {
  transition-timing-function: ease-in;
  position: absolute;
  top: 0;
  margin-top: -70px;
  opacity: 0;
  transition: 300ms;
}
.nav__mobile__v__search__links {
  transition-timing-function: ease-out;
  transition: 250ms;
  position: absolute;
}
.nav__mobile__v__search__results__r {
  border-bottom: 1px solid #424245;
  margin-top: 15px;
  transition: 300ms;
}
.nav__mobile__v__search__results_m {
  position: relative;
}
.nav__mobile__v__search__results {
  width: 100%;
  height: auto;
  /* padding: 5px 10px 10px 10px; */
}
.nav__mobile__v__search__results__links {
  width: auto;
  height: auto;
  padding: 10px 5px 0px 5px;
  /* margin-right: 52px; */
}

.nav__mobile__v__search__results__links > ul {
  display: block;
  margin-block-start: 0em;
  margin-block-end: 0em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  padding-inline-start: 0px;
}

.nav__mobile__v__search__results__links > ul > li {
  list-style: none;
  padding: 11px 22px 11px 0px;
  border-bottom: 1px solid #424245;
}
.nav__mobile__v__search__results__links > ul > li > a {
  background-position: left center;
  display: block;
  outline-offset: 0;
  padding: 0;
  width: auto;
  max-width: none;
  text-decoration: none;
  font-family: "sf pro text", "Myriad Set Pro", "SF Pro Icons", "Helvetica Neue",
    "Helvetica", "Arial", sans-serif;
  color: #d6d6d6;
  font-size: 16.2px;
  font-weight: 400;
  letter-spacing: -0.01em;
  position: relative;
  display: inline-block;
  background: no-repeat;
  text-decoration: none;
  white-space: nowrap;
  -webkit-tap-highlight-color: rgb(214 214 214);
  outline-offset: -7px;
  transition: 400ms;
  opacity: 0.9;
}
.nav__mobile__v__search__quick__l ul > li > a {
  font-size: 14px;
  color: #fff;
  opacity: 1;
}
.nav__mobile__v__search__quick__title {
  font-size: 13px;
  line-height: 1.33337;
  letter-spacing: -0.01em;
  font-family: "sf pro text", "Myriad Set Pro", "SF Pro Icons", "Helvetica Neue",
    "Helvetica", "Arial", sans-serif;
  text-transform: uppercase;
  color: #86868b;
  padding: 0 !important;
  margin-top: 10px;
  margin-bottom: 5px;
}
@media screen and (max-width: 1010px) {
  .nav__container-child {
    width: 900px;
  }
  .nav__links > ul > li {
    padding: 2px 30px 0px 15.5px;
  }
}

@media screen and (max-width: 960px) {
  .nav__container-child {
    width: 880px;
  }
  .nav__logo {
    left: 17px;
  }
  .nav__links > ul > li {
    padding: 2px 23px 0px 17.5px;
  }
}
@media screen and (max-width: 900px) {
  .nav__container-child {
    width: 800px;
    position: relative;
  }
  .nav__logo {
    left: -10px;
  }
  .nav__links > ul {
    position: relative;
    left: -30px;
  }
  .nav__links > ul > li {
    padding: 2px 23px 0px 17px;
  }
  .nav__links > ul > li:nth-child(8) {
    padding: 7px 27px 0px 0px;
  }
  .nav__links > ul > li:nth-child(9) {
    padding: 7px 27px 0px 0px;
  }
}
@media screen and (max-width: 830px) {
  .nav__container-child {
    width: 700px;
  }
  .nav__logo {
    left: -40px;
  }
  .nav__links > ul {
    left: -70px;
  }
  .nav__links > ul > li {
    padding: 2px 18px 0px 17.5px;
  }
}

@media screen and (max-width: 805px) {
  .nav__container {
    transition: 300ms;
  }
  .nav__mobile__v {
    width: 550px;
    margin: auto;
  }
  .nav__container-child {
    width: 100%;
  }

  .nav__links > ul {
    left: -60px;
  }
  .nav__links > ul > li {
    padding: 2px 15px 0px 15px;
  }
  .nav__links__m {
    opacity: 1 !important;
  }

  .nav__links {
    display: none;
  }

  .nav__mobile {
    display: block;
  }
  .nav__logo {
    left: 25px;
    width: 100%;
  }
  .nav__logo > img {
    margin: auto;
    display: block;
    height: 50px;
    transform: unset !important;
    opacity: 0.8;
  }
  .nav__container__overlay {
    display: none;
  }
  .nav__search__container {
    display: none;
  }
  .nav__search__results {
    width: 100%;
    position: relative;
    top: 2px;
  }
}
@media screen and (max-width: 620px) {
  .nav__mobile__v {
    width: 90%;
  }
}
